<template>
  <div class="about">
    <div id="header">
      <header-nav></header-nav>
    </div>
    <!-- 左边菜单区 -->
    <div class="content clearfix">

      <ul class="leftMenu">
        <li>关于我们</li>
        <li>友情链接</li>
        <li>联系我们</li>
        <li>帮助中心</li>
      </ul>

      <!-- 详细内容区 -->
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>联系我们</span>
          <span>招聘</span>
        </div>
        <div v-for="(item,index) in 4" :key="index" class="text item">
          {{'联系电话：13212345678 '}}
        </div>
      </el-card>
    </div>
    <div class="foot">
      <FooterBottom></FooterBottom>
    </div>
  </div>
</template>

<script>
  import headerNav from "@/components/headerNav";
  import FooterBottom from "@/components/footer";

  export default {
    components: {
      headerNav,
      FooterBottom,
    },

  }
</script>

<style scoped>
  .about {
    background-color: #f8f8f8;
  }

  .leftMenu {
    margin-top: 45px;
    background-color: #fff;
    width: 208px;
    border-radius: 3px;
    float: left;
  }

  .leftMenu li {
    padding: 15px;
    font-size: 13px;
    color: #333;
    line-height: 13px;
  }

  .el-card {
    float: left;
    margin: 45px 15px;
  }

  .el-card span {
    margin: 0 10px;
    font-weight: 600;
  }

  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }

  .foot {
    position: relative;
    top: 330px;
  }
</style>



